<template>
    <v-dialog width="350px" presistent v-model="editDialog">
        <v-btn dark class="red lighten-1" floating accent slot="activator">
            Edit Time
        </v-btn>
        <v-card>
            <v-container>
                <v-layout row wrap>
                    <v-flex xs12>
                        <v-card-title>Edit Meetup Time</v-card-title>
                    </v-flex>
                </v-layout>
                <v-divider></v-divider>
                <v-layout row wrap>
                    <v-flex xs12>
                        <v-time-picker v-model="editableTime" style="width: 100%;" actions format="24hr">
                            <template scope="{save, cancel}">
                                <v-btn dark class="red lighten-1" flat @click.native="onSaveChanges">Save</v-btn>
                                <v-btn dark class="red lighten-1" flat @click.native="editDialog = false">Close</v-btn>
                            </template>
                        </v-time-picker>
                    </v-flex>
                </v-layout>
            </v-container>
        </v-card>
    </v-dialog>
</template>

<script>
    import Moment from '../../../../node_modules/moment'
    export default {
        props: ['meetup'],
        data () {
            return {
                editDialog: false,
                editableTime: null
            }
        },
        methods: {
            onSaveChanges () {
                const newDate = new Date(this.meetup.date)
                const hours = this.editableTime.match(/^(\d+)/)[1]
                const minutes = this.editableTime.match(/:(\d+)/)[1]
                newDate.setHours(hours)
                newDate.setMinutes(minutes)
                this.$store.dispatch('updateMeetupData', {
                    id: this.meetup.id,
                    date: newDate
                })
            }
        },
        created () {
//            this.editableTime = new Date(this.meetup.date).toTimeString()
            this.editableTime = Moment(this.meetup.date).format('h:mm')
        }
    }
</script>


